@use '../utils/variables' as *;
@use '../utils/mixins' as *;

.working-zone {
  max-width: $maxWidthWork;
  margin: 0 auto;
  padding: 0 5px;
}

.invisible {
  overflow: hidden;
  height: 1px;
  width: 1px;
  border: 0;
  padding: 0;
  clip-path: insert(0px, 99.9%, 99.9%);
  font-size: 0px;
}

.inactive {
  pointer-events: none;
  opacity: 0.5;
}

.no-touch {
  pointer-events: none;
}

.hide {
  display: none;
}

.modal-window-background {
  filter: blur(10px);
  pointer-events: none;
}

.header-headline {
  @include header2;
  text-align: center;
}
.headline {
  @include header;
  text-align: center;
}

.animation {
  transition: all 0.5s;
  pointer-events: none;
}

.relocate-element {
  order: -2;
}

.disable {
  display: none;
}

.lds-hourglass {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}

.lds-hourglass:after {
  content: ' ';
  display: block;
  border-radius: 50%;
  width: 0;
  height: 0;
  margin: 8px;
  box-sizing: border-box;
  border: 32px solid white;
  border-color: white transparent white transparent;
  animation: lds-hourglass 1.2s infinite;
}

@keyframes lds-hourglass {
  0% {
    transform: rotate(0);
    animation-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
  }
  50% {
    transform: rotate(900deg);
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  }
  100% {
    transform: rotate(1800deg);
  }
}
