@use '../utils'as *;

.cards {
  margin-top: 50px;
  display: grid;
  grid-template-columns: repeat(3, auto);

  &__card {
    background: cornflowerblue;
    color: white;
    text-align: center;
    border: 2px solid black;
    padding: 30px;
    margin: 20px;
    width: 80%;

    &:hover,
    &:focus {
      cursor: pointer;
      // transform: scale(1.2, 1.2);
      background: rgb(27, 25, 148);
    }
    &_promoted {
      background: $cardPromotedGradient;
    }
    &__image-wrap {
      width: 200px;
    }
    &__image {
      max-width: 100%;
    }
    &__headline {
      margin-top: 0;
      @include header;
    }

    &__link {
      color: greenyellow;
    }
  }
}
