@use '../utils'as *;

.form {
  position: relative;
  text-align: center;
  width: 50%;
  margin: 40px auto 0;
  padding: 10px;

  &__input-wrap {
    position: relative;
    margin-bottom: 20px;
    display: flex;
    text-align: center;
    justify-content: flex-end;
    align-items: baseline;
  }

  &__input-radio-wrap {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 130px;
  }

  &__input-radio {
    margin-left: 10px;
  }

  &__input-label {
    margin: 0 10px 10px;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    text-align: right;
    align-items: center;
    // width: 400px;
    line-height: 26px;
  }

  &__input {
    width: 300px;
    margin-left: 40px;
  }

  #select {
    text-transform: uppercase;
  }

  &__submit-wrap {
    margin-top: 20px;
  }

  &__submit {
    background: $searchButtonGradient;
  }

  &__checkbox {
    width: 20px;
    height: 20px;
    margin-left: 100px;
  }

  &__error-msg {
    right: -170px;
    position: absolute;
    margin: 0;
    color: red;
  }

  &__success-msg {
    @include header;
    position: absolute;
    color: greenyellow;
    margin-top: 30px;
    width: 100%;
    text-align: center;
  }
}

.label-checkbox {
  width: 100%;
  text-align: start;
}
